/* 通用默认样式开始 */

* {
  box-sizing: border-box;
}

body {
  background: #eee;
  margin: 20px 0;
}

article {
  position: relative;
  width: 21cm;
  min-height: 29.7cm;
  background: white;
  margin: 0 auto;
  overflow: auto;
  padding: 1em;
}

p {
  margin: .3em 0;
}

h1 {
  font-size: 25px;
  margin-top: 0;
}

h4, h5, h6 {
  font-weight: normal;
}

ol, ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

strong {
  color: #f60;
}

a {
  color: #999;
  text-decoration: none;
  border-bottom: 1px solid;
}

p, li {
  color: #666;
}

h3, h4, h5, h6 {
  margin: 0;
}

.little-circle {
  margin-right: 14px;
  width: 6px;
  height: 6px;
  border: 1px solid;
  border-radius: 50%;
  margin-top: .5em;
}

/* 通用默认样式结束 */

.personal-info {
  position: relative;
}

.personal-info img {
  position: absolute;
  top: 0;
  right: 0;
}

.projects p {
  display: flex;
  color: #666;
}

.jobs-content>li {
  display: flex;
  margin: .3em 0;
}

.projects>ol {
  margin-top: 0;
  list-style-position: inside;
  padding: 0;
}

.projects>ol>li {
  margin-bottom: 24px;
}

.projects>ol>li>header {
  display: flex;
  justify-content: space-between;
}

.projects>ol>li h3 {
  display: inline-block;
}

.skills>.wrapper {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.skills>.wrapper> :first-child {
  margin-right: 24px;
}

.skills>.wrapper>ul {
  color: #666;
}

.skill-item, .other-item {
  display: flex;
  margin: .3em 0;
}

.skill-item>span:last-child {
  flex: 1;
}

.openSource header img {
  position: absolute;
  top: 0;
  right: 0;
}

.openSource>ul>li {
  margin-bottom: 24px;
}

.openSource header {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.open-source p {
  display: flex;
}

.jobs>ol>li {
  margin: 12px 0;
}

.jobs .company {
  line-height: 2em;
}

.jobs header {
  color: #666;
  margin-bottom: 12px;
}

.jobs .jobs-section {
  display: flex;
  justify-content: space-between;
}

.jobs .jobs-section>h4 {
  font-weight: 700;
}

.jobs header>h3 {
  font-size: 18px;
  color: #333;
}

.jobs header>h4 {
  margin-left: 1em;
  font-size: 18px;
}

.jobs header>span {
  font-size: 18px;
  margin-left: auto;
}

.others li {
  display: flex;
}

.others li span:last-child {
  flex: 1;
}

/* - 样式 */

.projects p>span:first-child, .jobs-content>li>span:first-child, .open-source p>span:first-child {
  padding-left: 3px;
  margin-right: 14px;
}

@media (max-width: 500px) {
  body {
    margin: 0;
  }
  article {
    width: auto;
    height: auto;
  }
  .personal-info img {
    display: none;
  }
  .jobs header {
    flex-direction: column;
  }
  .jobs header>h3, .jobs header>h4, .jobs header>span {
    margin: 0;
  }
}

.copy-success-tip {
  position: fixed;
  top: 80%;
  left: 50%;
  padding: 5px 10px;
  color: rgba(255, 255, 255, .8);
  background-color: rgba(0, 0, 0, .4);
  border-radius: 5px;
  transform: translate(-50%, -50%);
  opacity: 1;
  transition: opacity 1s;
}

#phone, #wechat, #email {
  cursor: pointer;
}